<template>
  <div class="detail">
    <div class="bg_light padding_v_20">
      <div class="main_width flex">
        <div class="flex_1 margin">
          <img :src="ico">
        </div>
        <div class="margin_l_20 light">
          <div class="margin_b_20 padding_15_20 bg_white">
            <div class="font_bold black font_16 lineheight_2">项目评分</div>
            <div class="flex_center">
              <div class="padding_h_15 border_r">
                <div class="orange font_bold" style="font-size:50px;">{{ parseFloat(project.project_rating.total_score).toFixed(1) }}</div>
                <div class="orange font_16">综合评分</div>
              </div>
              <div class="flex_1 padding_h_15 lineheight_2">
                <div class="flex" v-for="(s,k) in project.project_rating">
                  <template v-if="score_nav[k]">
                    <div>{{ score_nav[k] + '：' }}</div>
                    <Star :score="parseFloat(s)"></Star>
                  </template>
                </div>
              </div>
            </div>
          </div>
          <div class="padding_15_20 bg_white">
            <div class="font_bold black font_16 lineheight_2">项目进展</div>
            <div class="lineheight_2">
              <div v-for="(p,k) in project" v-if="project_nav[k]">
                <template>{{ project_nav[k] }}：</template>
                <a :href="p" v-if="k==='project_web'">{{ p }}</a>
                <template v-else-if="k==='status'">{{ p===1?'已发布':p===2?'已结束':'未开始' }}</template>
                <template v-else>{{ p }}</template>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="border_bottom">
      <div class="flex main_width padding_t_10">
        <div class="width_100 text_center hover lineheight_3" v-for="(n,k) in nav">
          <router-link :to="n" :class="[{'black font_bold border_b text': ($route.path === n)}, 'light', 'block_inline', 'padding_b_5']">{{ k }}</router-link>
        </div>
      </div>
    </div>
    <div style="margin:70px 0">
      <div class="main_width flex">
        <div class="flex_1">
          <router-view></router-view>
        </div>
        <div>
          <div class="border circle_5" style="width:340px;overflow:hidden">
            <div class="bg_light text_center border_b padding_v_10 font_16">
              <div class="font_bold">官方公示</div>
              <div class="font_12 light" v-if="own">所有官方公示数据已由保全网保全</div>
            </div>
            <div class="padding_h_15" v-if="own">
              <div :class="[{border_b: i !== 4}, 'flex', 'padding_v_10']" v-for="i in 4">
                <div class=""><img class="fit_img" :src="ico" style="width:66px;height:66px"></div>
                <div class="padding_l_10 flex_1">
                  <div>重要通知：路印协议推出“LRC社区激励计划”</div>
                  <div class="font_12 light">10:22</div>
                </div>
              </div>
            </div>
            <div v-else>
              <div class="padding_15">
                <div>尚未有团队对此项目进行认领，现在可以享受官方认证，公示数据免费保全服务。</div>
              </div>
              <div class="btn_block btn_blue cursor lineheight_3" style="border-radius:0" @click="doOwn">立即认领</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api'
import Star from '@/components/Parts/Star'
export default {
  name: 'detail',
  data () {
    return {
      ico: require('@/assets/images/detail/ico.png'),
      score_nav: {project_team_score: '项目团队', technology_score: '技术能力', safety_score: '安全保障', business_score: '商业逻辑', plan_score: '发行方案'},
      project_nav: {project_main: '发起主体', project_web: '项目官网', ico_amount: 'ICO金额', coin_amount: '代币总量', status: '当前状态'},
      nav: {'项目主页': '/Detail/' + this.$route.params.proId + '/Introduction', '项目评级': '/Detail/' + this.$route.params.proId + '/Grade', '项目报道': '/Detail/' + this.$route.params.proId + '/Stuation', '项目评论': '/Detail/' + this.$route.params.proId + '/Comment'},
      own: false,
      project: {project_rating: {total_score: 0}}
    }
  },
  created () {
    console.log(this.$route)
    window.scrollTo(0, 0)
    api.post('detail/head_info', {sign: 'token=0&project_id=1'}).then(res => {
      console.log(res)
      this.project = res
    })
  },
  methods: {
    doOwn () {
      this.own = !this.own
    }
  },
  components: {
    Star
  }
}
</script>

<style type="text/css">
  .padding_b_5{
    padding-bottom: 10px
  }
  .padding_t_10{
    padding-top: 15px
  }
  .border_b.text:after{
    background-color: #000;
    height: 2px;
  }
  .border_bottom{
    border-bottom: 1px solid #eff1f4
  }
</style>



